<template>
  <div class="page-resume-position">
    <p class="resume-block-title">职业信息专栏（仅限营销司机应聘人员填写）</p>
    <el-form label-width="120px" label-position="left"
      :model="positionInfo"
      :rules="rules"
      ref="positionInfo"
      >
      <div class="item-block">
        <el-form-item label="驾照类型" label-width="80px" prop="driveType">
          <el-select v-model="positionInfo.driveType" placeholder="请选择驾照类型">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="领证地点" label-width="80px" prop="certifacateAddress">
         <el-input placeholder="请输入领证地点" v-model="positionInfo.certifacateAddress">
         </el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="有效日期" label-width="80px" prop="expirDate">
          <el-date-picker
            v-model="positionInfo.expirDate"
            type="date"
            placeholder="请选择到期时间"
            :picker-options="pickerOptions0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="实际驾龄" label-width="80px" prop="driveTime">
         <el-input placeholder="请输入实际驾龄" v-model="positionInfo.driveTime">
         </el-input>
        </el-form-item>
      </div>

      <el-form-item label="驾驶过的车辆类型" label-width="160px" prop="driveCarList">
        <el-checkbox-group v-model="positionInfo.driveCarList">
            <el-checkbox label="1.5吨以上货车"></el-checkbox>
            <el-checkbox label="其他"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="有无货运上岗证" label-width="160px" prop="hasWorkLicense">
        <el-radio-group v-model="positionInfo.hasWorkLicense">
            <el-radio label="有"></el-radio>
            <el-radio label="无"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="货运上岗证有效日期至" label-width="180px" prop="workLicenseExpirDate">
        <el-date-picker
          v-model="positionInfo.workLicenseExpirDate"
          type="date"
          placeholder="请输入到期时间"
          :picker-options="pickerOptions0">
        </el-date-picker>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="next('positionInfo')">
          下一步
          <i class="el-icon-arrow-right el-icon--right">
          </i>
        </el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        positionInfo: {
          driveType: '',
          certifacateAddress: '',
          expirDate: '',
          driveTime: '',
          driveCarList: [],
          hasWorkLicense: '',
          workLicenseExpirDate: ''
        },
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() > Date.now()
          }
        },
        options: [{
          value: 'A1',
          label: 'A1'
        }, {
          value: 'A2',
          label: 'A2'
        }, {
          value: 'A3',
          label: 'A3'
        }, {
          value: 'B1',
          label: 'B1'
        }, {
          value: 'B2',
          label: 'B2'
        }, {
          value: 'C1',
          label: 'C1'
        }, {
          value: 'C2',
          label: 'C2'
        }, {
          value: 'C3',
          label: 'C3'
        }, {
          value: 'C4',
          label: 'C4'
        }, {
          value: 'D',
          label: 'D'
        }, {
          value: 'E',
          label: 'E'
        }, {
          value: 'F',
          label: 'F'
        }, {
          value: 'M',
          label: 'M'
        }, {
          value: 'N',
          label: 'N'
        }, {
          value: 'P',
          label: 'P'
        }],
        rules: {
          driveType: [
            {required: true, message: '请选择驾照类型', trigger: 'change'}
          ],
          certifacateAddress: [
            {required: true, message: '请输入领证地址', trigger: 'blur'}
          ],
          expirDate: [
            {required: true, message: '请选择驾照到期时间', trigger: 'change', type: 'date'}
          ],
          driveTime: [
            {required: true, message: '请输入实际驾龄', trigger: 'blur'}
          ],
          driveCarList: [
            {required: true, message: '请选择驾驶过的车辆类型', trigger: 'change', type: 'array'}
          ],
          hasWorkLicense: [
            {required: true, message: '请选择是否有货运上岗证', trigger: 'change'}
          ],
          workLicenseExpirDate: [
            {required: true, message: '请选择货运上岗证到期时间', trigger: 'change', type: 'date'}
          ]
        }
      }
    },
    created () {
      if (sessionStorage.positionInfo) {
        var positionInfo = JSON.parse(sessionStorage.positionInfo)
        positionInfo.expirDate = new Date(positionInfo.expirDate)
        positionInfo.workLicenseExpirDate = new Date(positionInfo.workLicenseExpirDate)
        this.positionInfo = positionInfo
      }
    },
    methods: {
      prev () {
        this.$router.push({name: 'base-info'})
      },
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            sessionStorage.setItem('positionInfo', JSON.stringify(this.positionInfo))
            this.$router.push({name: 'contact'})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-position
    padding-bottom: 50px
</style>



