  <template>
  <div class="page-user-notsatisfied">
    <div class="quickly-enter">
      <el-dropdown>
        <el-button size="small" type="primary" plain>
          移动至<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>人才库</el-dropdown-item>
          <el-dropdown-item>不合格库</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button size="small" type="primary">安排面试</el-button>
    </div>
    <div class="form-block-title">
      <el-menu :default-active="activeIndex" mode="horizontal" @select="handleSelect">
        <el-menu-item index="1">全部</el-menu-item>
        <el-menu-item index="2">运营管理</el-menu-item>
        <el-menu-item index="3">招聘经理</el-menu-item>
      </el-menu>
      <el-button @click="addCustom" class="btn-custom" icon="el-icon-plus">自定义分类</el-button>
    </div>
    <form-card></form-card>
    <div class="btn-container">
      <el-button icon="el-icon-upload2" type="primary" size="small">导出</el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        width="80">
      </el-table-column>
      <el-table-column
        label="性别"
        width="50">
        <template slot-scope="scope">{{ scope.row.gender }}</template>
      </el-table-column>
      <el-table-column
        prop="address"
        label="应聘岗位"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="birthday"
        label="出生日期"
        width="100"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="telphone"
        label="联系电话"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="married"
        label="婚姻状况"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="isInterview"
        label="是否面试"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="deliverDate"
        label="加入时间"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="150">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">安排面试</el-button>
          <el-button type="text" size="small">移动至</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="100"
        layout="total, prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import formCard from './form-card'

  export default {
    data () {
      return {
        activeIndex: '1',
        activeIndex2: '2',
        tableData3: [{
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          deliverDate: '2017/08/08',
          isInterview: '是',
          status: '存档备用'
        }, {
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          deliverDate: '2017/08/08',
          isInterview: '是',
          status: '存档备用'
        }, {
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          deliverDate: '2017/08/08',
          isInterview: '否',
          status: '存档备用'
        }, {
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          deliverDate: '2017/08/08',
          isInterview: '是',
          status: '存档备用'
        }],
        multipleSelection: [],
        currentPage: 1
      }
    },
    methods: {
      addCustom () {
        this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$message({
            type: 'success',
            message: '删除成功'
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      toggleSelection (rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row)
          })
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      handleClick (row) {
        console.log(row)
      },
      handleSizeChange (val) {
        console.log(`每页 ${val} 条`)
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      handleSelect (key, keyPath) {
        console.log(key, keyPath)
      }
    },
    components: { formCard }
  }
</script>

<style lang="stylus">
  .page-user-notsatisfied
    padding-bottom: 30px
    .form-block-title
      position: relative
      margin-bottom: 10px
      border-bottom: 1px solid #ddd
      h3
        padding-bottom: 10px
        font-size: 16px
        font-weight: 500
    .btn-custom
      position: absolute
      left: 350px
      top: 50%
      transform: translateY(-50%)
      border: 0
    .notsatisfied-el-menu
      display: inline-block
    .btn-container
      padding: 10px 0 15px
      text-align: right
    .pagination-container
      padding-top: 30px
      text-align: center
    .el-menu--horizontal,
    .el-menu--horizontal>.el-submenu.is-active .el-submenu__title
      border-bottom: 0
    .el-menu--horizontal .el-submenu>.el-menu
      text-align: left
      width: 100%
    .quickly-enter
      padding-top: 25px
      display: flex
      align-items: center
      justify-content: flex-end
      .el-button
        margin-left: 15px
</style>



