<template>

  <el-form class="user-form-card" label-width="70px" label-position="right" size="mini" :model="cardform" ref="cardform" :rules="rules">
    <div class="item-block">
      <el-form-item label="申请人" prop="name">
        <el-input v-model="cardform.name" placeholder="申请人姓名" ></el-input>
      </el-form-item>
      <el-form-item label="应聘岗位" prop="applicationArea">
        <el-select v-model="cardform.applicationArea" >
          <el-option v-for="item in applicationAreaOptions"
            :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="应聘区域" prop="ypqy">
        <el-select v-model="cardform.ypqy"   >
          <el-option v-for="(item, index) in ypqy"
            :key="index" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item class="form-item-date" prop="tdrq"
        label="简历投递日期" label-width="120px">
        <div class="block">
          <el-date-picker

            v-model="cardform.tdrq"
            type="daterange"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
      </el-form-item>
    </div>
    <div class="item-block">
      <!--<div style="margin-top: 10px;margin-left: 0px">-->
      <el-form-item label="性别"  prop="sex">
        <el-radio-group v-model="cardform.sex"  >
          <el-radio
            v-for="(item, index) in sex"
            :label="item.label"
            :key="index">
            {{ item.label }}</el-radio>
        </el-radio-group>
      </el-form-item>
    <!--</div>-->
      <div style="margin-right: 100px;">
      <el-form-item label="婚姻状况" prop="merryStatus" >
        <el-radio-group v-model="cardform.merryStatus"   >
          <el-radio
            v-for="(item, index) in merryStatus"
            :label=item.label
            :key="index">
            {{ item.label }}
          </el-radio>
        </el-radio-group>
      </el-form-item>
      </div>
      <div style="margin-left: 20px">
      <el-form-item class="form-item-date" prop="csrq"
        label="出生日期" label-width="100px">
        <div class="block">
          <el-date-picker
            v-model="cardform.csrq"

            type="daterange"
            range-separator="-"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </div>
      </el-form-item>
    </div>
    </div>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary"  v-on:click="submit('cardform')">检索</el-button>
        <el-button  @click="resetForm('cardform')">重置</el-button>
      </el-button-group>
    </div>
  </el-form>
</template>

<script>
  export default {
    data () {
      return {
        cardform: {
          name: '',
          applicationArea: '',
          ypqy: '',
          tdrq: '',
          endDate: '',
          sex: [],
          merryStatus: [],
          csrq: '',
        },
        applicationAreaOptions: [{
          label: '数据工程师',
          value: 1
        }, {
          label: 'Java开发工程师',
          value: 2
        }],
        sex: [
          {label: '男'},
          {label: '女'}
        ],
        merryStatus: [
          {label: '未婚'},
          {label: '已婚未育'},
          {label: '已婚已育'}
        ],
        ypqy: [{
          label: '上海',
          value: 1
        }, {
          label: '北京',
          value: 2
        }]
      }
    },
    methods: {
      prev () {
        this.$router.push({name: 'resume'})
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      },
      submit (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var data = {
              name: this.cardform.name,
              applicationArea: this.cardform.ypqy,
              sex: this.cardform.sex,
              marriage: this.cardform.merryStatus,
              pageNum: '0',
              pageSize: '10'
            }
            console.log(JSON.stringify(data))
            this.$http.post('/candidate/query', JSON.stringify(data), {emulateJSON: true})
              .then(
                (response) => {
                  console.log(response)
                  sessionStorage.setItem('candidate', JSON.stringify(response.data.data))
                  sessionStorage.setItem('total', JSON.stringify(response.data.total))
                  this.$emit('loadData', sessionStorage.candidate)
                  console.log('total: ' + JSON.parse(sessionStorage.total))
                  console.log('=========' + JSON.parse(sessionStorage.candidate))
                }
              )
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .user-form-card
    border: 1px solid #ddd
    padding: 10px 1px 0px 1px
    margin-bottom: 35px
    .item-block
      display: flex
      &.btn-container
        justify-content: center
      .el-form-item
        flex: 1
    .el-input
      width: 130px
    .el-date-editor--daterange.el-input__inner
      width: 170px


</style>



