<template>
  <div class="page-resume-technology">
    <p class="resume-block-title">技能及个人特长</p>
    <el-form :model="skill" ref="skill">
      <el-form-item label="您会使用何种电脑软件?（例如：EXCEL,WORD,PPT等）" 
      prop="software"
      :rules="[
        {required: true, message: '请输入电脑软件'}
      ]">
        <!-- <el-checkbox-group v-model="skill.software.softwareList">
          <el-checkbox label="EXCEL"></el-checkbox>
          <el-checkbox label="WORD"></el-checkbox>
          <el-checkbox label="PPT"></el-checkbox>
        </el-checkbox-group> -->
        <el-input type="textarea" placeholder="如有其他请填写" v-model="skill.software">
        </el-input>
      </el-form-item>

      <el-form-item label="曾经参加过何种专业技能培训 ?" prop="training"
        :rules="[
          {required: true, message: '请输入专业技能'}
        ]"
        >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入" 
        v-model="skill.training"></el-input>
      </el-form-item>

      <el-form-item label="外语水平 / 其他技能" prop="languageLevel"
        :rules="[
          {required: true, message: '请输入外语水平'}
        ]"
        >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"
        v-model="skill.languageLevel"></el-input>
      </el-form-item>

      <el-form-item label="个人兴趣爱好、特长" prop="interest"
        :rules="[
          {required: true, message: '请输入个人兴趣'}
        ]"
        >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入兴趣爱好"
        v-model="skill.interest"></el-input>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="next('skill')">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        skill: {
          software: '',
          training: '',
          languageLevel: '',
          interest: ''
        },
        textareaSize: { minRows: 5, maxRows: 8 }
      }
    },
    created () {
      if (sessionStorage.skill) {
        this.skill = JSON.parse(sessionStorage.skill)
      }
    },
    methods: {
      prev () {
        this.$router.push({name: 'work'})
      },
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            sessionStorage.setItem('skill', JSON.stringify(this.skill))
            this.$router.push({name: 'shengao'})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-technology
    padding-bottom: 50px
</style>



