<template>
  <div class="page-user-interview">
    <div class="quickly-enter">
      <el-dropdown>
        <el-button size="small" type="primary" plain>
          移动至<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>人才库</el-dropdown-item>
          <el-dropdown-item>不合格库</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="form-block-title">
      <h3>面试管理</h3>
    </div>
    <form-card></form-card>
    <div class="btn-container">
      <el-button icon="el-icon-upload2" type="primary" size="small">导出</el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="name"
        label="面试对象"
        width="80">
      </el-table-column>
      <el-table-column
        label="性别"
        width="50">
        <template slot-scope="scope">{{ scope.row.gender }}</template>
      </el-table-column>
      <el-table-column
        prop="post"
        label="应聘岗位"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="date_of_birth"
        label="出生日期"
        width="100"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="marriage"
        label="婚姻状况"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="phone"
        label="联系电话"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="interviewDate"
        label="本次面试时间"
        width="120"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="majorName"
        label="面试人"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="result"
        label="面试结果"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="250">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">查看评价</el-button>
          <el-button type="text" size="small">移动至</el-button>
          <el-button type="text" size="small">创建录用申请</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="100"
        layout="total, prev, pager, next"
        :total="1000">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import formCard from './form-card'

  export default {
    data () {
      return {
        tableData3: [{
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          position: '运营管理',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '吴娇',
          result: '合格'
        }, {
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          position: '运营管理',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '吴娇',
          result: '合格'
        }, {
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          position: '运营主管助理',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '吴娇',
          result: '合格'
        }, {
          gender: '男',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄',
          position: '运营主管助理',
          birthday: '1990/11/11',
          married: '未婚',
          telphone: '151 2681 1895',
          interviewTime: '2017/08/08',
          majorName: '吴娇',
          result: '合格'
        }],
        multipleSelection: [],
        currentPage: 1
      }
    },
    methods: {
      toggleSelection (rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row)
          })
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      handleClick (row) {
        console.log(row)
      },
      handleSizeChange (val) {
        console.log(`每页 ${val} 条`)
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      handleSelect (key, keyPath) {
        console.log(key, keyPath)
      }
    },
    components: { formCard }
  }
</script>

<style lang="stylus">
  .page-user-interview
    padding-bottom: 30px
    .form-block-title
      padding-bottom: 15px
      margin-bottom: 10px
      border-bottom: 1px solid #ddd
      h3
        padding-bottom: 10px
        font-size: 16px
        font-weight: 500
    .interview-el-menu
      display: inline-block
    .btn-container
      padding: 10px 0 15px
      text-align: right
    .pagination-container
      padding-top: 30px
      text-align: center
    .el-menu--horizontal,
    .el-menu--horizontal>.el-submenu.is-active .el-submenu__title
      border-bottom: 0
    .el-menu--horizontal .el-submenu>.el-menu
      text-align: left
      width: 100%
    .quickly-enter
      padding-top: 25px
      display: flex
      align-items: center
      justify-content: flex-end
      .el-button
        margin-left: 15px
</style>



