<template>
  <div class="page-resume-education">
    <p class="resume-block-title">教育经历（从高中写起）</p>
    <el-form label-width="80px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in educationList" :key="index">
        <div class="item-block">
          <el-form-item label="起始年月">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              @change="validate(item.startDate, '起始年月')"
              placeholder="请输入起始年月">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="终止年月">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              @change="validate(item.endDate, '终止年月')"
              placeholder="请输入终止年月">
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="学校名称">
            <el-input v-model="item.schoolName" placeholder="请输入学校名称"
            @blur="validate(item.schoolName, '学校名称')"></el-input>
          </el-form-item>
          <el-form-item label="专业">
            <el-input v-model="item.majorName" placeholder="请输入专业名称"
            @blur="validate(item.majorName, '专业名称')"></el-input>
          </el-form-item>
        </div>

        <el-form-item label="学历">
          <el-select v-model="item.xl" @change="validate(item.xl, '学历')">
            <el-option v-for="(item, index) in educationOptions"
              :key="index" :label="item.label" :value="item.label"></el-option>
          </el-select>
        </el-form-item>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus">新增教育经历</el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="next">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        educationList: [{
          startDate: '',
          endDate: '',
          schoolName: '',
          majorName: '',
          xl: ''
        }],
        educationOptions: [{
          label: '高中'
        }, {
          label: '中专'
        }, {
          label: '大专'
        }, {
          label: '本科'
        }, {
          label: '硕士'
        }, {
          label: '博士'
        }]
      }
    },
    created () {
      if (sessionStorage.educationList) {
        var educationList = JSON.parse(sessionStorage.educationList)
        for (let i = 0; i < educationList.length; i++) {
          educationList[i].startDate = new Date(educationList[i].startDate)
          educationList[i].endDate = new Date(educationList[i].endDate)
        }
        this.educationList = educationList
      }
    },
    methods: {
      addList () {
        if (this.educationList.length < 3) {
          this.educationList.push({
            startDate: '',
            endDate: '',
            schoolName: '',
            majorName: '',
            xl: ''
          })
        } else {
          this.$message('最多填写3段教育经历')
        }
      },
      validate (item, value) {
        if (!item) {
          console.log(item)
          this.$message('请输入' + value)
        }
      },
      prev () {
        this.$router.push({name: 'family'})
      },
      next () {
        for (let i = 0; i < this.educationList.length; i++) {
          for (let item in this.educationList[i]) {
            if (!this.educationList[i][item]) {
              console.log(item)
              this.$message('请填写相关信息')
              return
            }
          }
          if (this.educationList[i].startDate.getTime() >= this.educationList[i].endDate.getTime()) {
            this.$message('起始年月不能大于终止年月')
            return
          }
        }
        sessionStorage.setItem('educationList', JSON.stringify(this.educationList))
        this.$router.push({name: 'work'})
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-education
    padding-bottom: 50px
</style>



