<template>
  <div class="page-resume-family">
    <p class="resume-block-title">家庭成员（已婚者须填写配偶、子女情况；未婚者填写父母及兄弟姐妹）</p>
    <el-form label-width="80px" label-position="left" class="demo-dynamic">
      <div class="form-item-group"
        v-for="(item, index) in familyMembers"
        :key="item.key">
        <div class="item-block">
          <el-form-item label="姓名">
            <el-input v-model="item.name" placeholder="请输入成员姓名" @blur="validate(item.name, '姓名')">
            </el-input>
          </el-form-item>
          <el-form-item label="关系">
            <el-input v-model="item.relation" placeholder="请输入关系" @blur="validate(item.relation, '关系')"></el-input>
          </el-form-item>
        </div>

         <div class="item-block">
          <el-form-item label="年龄">
            <el-input v-model.number="item.age" placeholder="请输入成员年龄" @blur="validate(item.age, '年龄')"></el-input>
          </el-form-item>
          <el-form-item label="居住地">
            <el-input v-model="item.address" placeholder="请输入成员居住地" @blur="validate(item.address, '居住地')"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="工作单位">
            <el-input v-model="item.workPlace" placeholder="请输入成员工作单位" @blur="validate(item.workPlace, '工作单位')"></el-input>
          </el-form-item>
          <el-form-item label="职务">
            <el-input v-model="item.position" placeholder="请输入成员职务" @blur="validate(item.position, '成员职务')"></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addMembers" icon="el-icon-plus">新增家庭成员</el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="next('familyMembers')">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        familyMembers: [{
          name: '',
          relation: '',
          age: '',
          address: '',
          workPlace: '',
          position: ''
        }]
      }
    },
    created () {
      if (sessionStorage.familyMembers) {
        this.familyMembers = JSON.parse(sessionStorage.familyMembers)
      }
    },
    methods: {
      addMembers () {
        if (this.familyMembers.length < 3) {
          this.familyMembers.push({
            name: '',
            relation: '',
            age: '',
            address: '',
            workPlace: '',
            position: ''
          })
        } else {
          this.$message('最多填写3个家庭成员')
        }
      },
      validate (item, value) {
        if (!item) {
          this.$message('请输入' + value)
          return
        }
        if (value === '年龄') {
          if (!/^[1-9]\d*$/.test(item)) {
            this.$message('请输入正整数')
          } else {
            if (item < 18 || item > 100) {
              this.$message('年龄不能小于18且不能大于100')
            }
          }
        }
      },
      prev () {
        this.$router.push({name: 'contact'})
      },
      next () {
        for (let i = 0; i < this.familyMembers.length; i++) {
          for (let item in this.familyMembers[i]) {
            if (!this.familyMembers[i][item]) {
              this.$message('请填写相关信息')
              return
            }
          }
        }
        sessionStorage.setItem('familyMembers', JSON.stringify(this.familyMembers))
        this.$router.push({name: 'education'})
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-family
    padding-bottom: 50px
</style>



