<template>

  <div class="page-user-resume">
    <div class="quickly-enter">
      <el-dropdown>
        <el-button size="small" type="primary" plain>
          移动至<i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>人才库</el-dropdown-item>
          <el-dropdown-item>不合格库</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button size="small" type="primary">创建面试</el-button>
    </div>
    <div class="form-block-title">
      <h3>简历管理</h3>
      <p>可点击姓名链接查看具体简历内容</p>
    </div>
    <form-card  v-on:loadData="loadTable"></form-card>
    <div class="btn-container">
      <el-button icon="el-icon-upload2" type="primary" size="small">导出</el-button>
    </div>
    <el-table
      ref="multipleTable"
      :data="candidate"
      tooltip-effect="dark"
      style="width: 100%"
      :row-class-name="tableRowClassName"
      @selection-change="handleSelectionChange">
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="candidate.name"
        label="姓名"
        width="120">
      </el-table-column>
      <el-table-column
        prop="sex"
        label="性别"
        width="50">
        <template slot-scope="scope">{{ scope.row.gender }}</template>
      </el-table-column>
      <el-table-column
        prop="post"
        label="应聘岗位"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="date_of_birth"
        label="出生日期"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="marriage"
        label="婚姻状况"
        width="80"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="phone"
        label="联系电话"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="deliverDate"
        label="简历投递时间"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="150">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">创建面试</el-button>
          <el-button type="text" size="small">移动至</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-size="6"
        layout="total, prev, pager, next"
        :total="10">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import formCard from './form-card'
  export default {

    data () {
      return {
        candidate: [
        ]
      }
    },
    create () {
    },
    methods: {
      loadTable (data) {
        console.log('+++++++++++++++' + JSON.parse(data))
        this.candidate.push(data[0])
        alert('1111111111')
      },
      tableRowClassName ({row, rowIndex}) {
        if (rowIndex % 2 === 0) {
          return 'warning-row'
        } else {
          return ''
        }
      },
      toggleSelection (rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row)
          })
        } else {
          this.$refs.multipleTable.clearSelection()
        }
      },
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      handleClick (row) {
        console.log(row)
      },
      handleSizeChange (val) {
        console.log(`每页 ${val} 条`)
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      handleSelect (key, keyPath) {
        console.log(key, keyPath)
      }
    },
    components: { formCard },
  }
</script>

<style lang="stylus">
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #ffffff;
  }
  .page-user-resume
    padding-bottom: 30px
    .form-block-title
      padding-bottom: 15px
      margin-bottom: 10px
      border-bottom: 1px solid #ddd
      h3
        padding-bottom: 10px
        font-size: 16px
        font-weight: 500
    .resume-el-menu
      display: inline-block
    .btn-container
      padding: 0px 0 15px
      text-align: right
    .pagination-container
      padding-top: 30px
      text-align: center
    .el-menu--horizontal,
    .el-menu--horizontal>.el-submenu.is-active .el-submenu__title
      border-bottom: 0
    .el-menu--horizontal .el-submenu>.el-menu
      text-align: left
      width: 100%
    .quickly-enter
      padding-top: 25px
      display: flex
      align-items: center
      justify-content: flex-end
      .el-button
        margin-left: 15px
</style>



