<template>
  <div class="page-resume-baseinfo">
    <el-form label-width="80px" label-position="left" :model="baseInfo" ref="baseInfo" :rules="rules">
      <div class="item-block">
        <el-form-item label="申请职位" prop="applyPosition">
          <el-select v-model="baseInfo.applyPosition" placeholder="请选择申请职位">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="应聘区域" prop="applicationArea">
          <el-select v-model="baseInfo.applicationArea" placeholder="请选择申请职位">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="可到职日" prop="entryDate">
          <el-date-picker
            v-model="baseInfo.entryDate"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions0">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="期望薪资" prop="expectSalary">
          <el-input placeholder="请输入期望薪资" v-model="baseInfo.expectSalary"></el-input>
        </el-form-item>
      </div>

      <el-form-item label="招聘渠道" prop="channelList">
        <el-checkbox-group v-model="baseInfo.channelList">
          <el-checkbox label="网络招聘"></el-checkbox>
          <el-checkbox label="人才市场"></el-checkbox>
          <el-checkbox label="中介公司"></el-checkbox>
          <el-checkbox label="内部引荐"></el-checkbox>
          <el-checkbox label="其他"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <p class="resume-block-title">基本信息</p>
      <div class="item-block">
        <el-form-item label="姓名" prop="name">
          <el-input placeholder="请输入姓名" v-model="baseInfo.name"></el-input>
        </el-form-item>
        <el-form-item label="性别" prop="gender">
          <el-select v-model="baseInfo.gender">
            <el-option v-for="(item, index) in genderOptions"
              :key="index" :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="出生年月" prop="birth">
          <el-date-picker
            v-model="baseInfo.birth"
            type="date"
            placeholder="请输入出生年月"
            :picker-options="pickerOptions1">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="身份证号" prop="idNumber">
         <el-input placeholder="请输入身份证号" v-model="baseInfo.idNumber"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="身高" prop="height">
         <el-input placeholder="请输入身高" v-model="baseInfo.height"></el-input>
        </el-form-item>
        <el-form-item label="体重" prop="weight">
         <el-input placeholder="请输入体重" v-model="baseInfo.weight"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="籍贯" prop="address">
         <el-input placeholder="请输入籍贯" v-model="baseInfo.address"></el-input>
        </el-form-item>
        <el-form-item label="政治面貌" prop="politicStatus">
          <el-select v-model="baseInfo.politicStatus">
            <el-option v-for="(item, index) in politicsOptions"
              :key="index" :label="item.label"
              :value="item.label">
            </el-option>
          </el-select>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="婚姻状况" prop="merryStatus">
          <el-radio-group v-model="baseInfo.merryStatus">
            <el-radio
              v-for="(item, index) in checkMarriedList"
              :label=item.label
              :key="index">
              {{ item.label }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="是否有服兵役" label-width="120px" prop="isSoldier">
          <el-radio-group v-model="baseInfo.isSoldier">
            <el-radio
              v-for="(item, index) in soldierList"
              :label="item.label"
              :key="index">
              {{ item.label }}
            </el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <div class="item-block btn-container">
        <el-button type="primary"  @click="next('baseInfo')">下一步</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
  import VHeader from './header'

  export default {
    data () {
      var checkIdNumber = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入身份证号'))
        } else if (!/\d{17}[\d|x]|\d{15}/i.test(value)) {
          callback(new Error('你输入的身份证长度或格式错误'))
        } else {
          callback()
        }
      }
      var checkWeight = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入重量'))
        } else if (!/^[1-9]\d*$/.test(value)) {
          callback(new Error('请输入正整数'))
        } else {
          callback()
        }
      }
      var checkHeight = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入身高'))
        } else if (!/^[1-9]\d*$/.test(value)) {
          callback(new Error('请输入正整数'))
        } else {
          callback()
        }
      }
      return {
        baseInfo: {
          applyPosition: '',
          applicationArea: '',
          entryDate: '',
          expectSalary: '',
          channelList: [],
          name: '',
          gender: 0,
          birth: '',
          idNumber: '',
          height: '',
          weight: '',
          address: '',
          politicStatus: '',
          merryStatus: '未婚',
          isSoldier: '否'
        },
        options: [{
          value: '1',
          label: '黄金糕'
        }, {
          value: '2',
          label: '双皮奶'
        }, {
          value: '3',
          label: '蚵仔煎'
        }, {
          value: '4',
          label: '龙须面'
        }, {
          value: '5',
          label: '北京烤鸭'
        }],
        pickerOptions0: {
          disabledDate (time) {
            return time.getTime() < Date.now()
          }
        },
        pickerOptions1: {
          disabledDate (time) {
            return time.getTime() > Date.now()
          }
        },
        genderOptions: [{
          label: '男',
          value: 0
        }, {
          label: '女',
          value: 1
        }],
        politicsOptions: [{
          label: '团员'
        }, {
          label: '党员'
        }, {
          label: '群众'
        }],
        checkMarriedList: [
          {label: '未婚'},
          {label: '已婚未育'},
          {label: '已婚已育'}
        ],
        soldierList: [
          {label: '是'},
          {label: '否'}
        ],
        rules: {
          applyPosition: [
            {required: true, message: '请选择申请职位', trigger: 'change'}
          ],
          applicationArea: [
            {required: true, message: '请选择应聘区域', trigger: 'change'}
          ],
          entryDate: [
            {required: true, message: '请选择入职时间', trigger: 'change', type: 'date'}
          ],
          expectSalary: [
            {required: true, message: '请输入期望薪资', trigger: 'blur'}
          ],
          channelList: [
            {required: true, message: '请选择至少一条应聘渠道', trigger: 'change', type: 'array'}
          ],
          name: [
            {required: true, message: '请输入姓名', trigger: 'blur'}
          ],
          birth: [
            {required: true, message: '请选择出生日期', trigger: 'change', type: 'date'}
          ],
          idNumber: [
            {validator: checkIdNumber, trigger: 'blur'}
          ],
          height: [
            {validator: checkHeight, trigger: 'blur'}
          ],
          weight: [
            {validator: checkWeight, trigger: 'blur'}
          ],
          address: [
            {required: true, message: '请输入籍贯', trigger: 'blur'}
          ],
          politicStatus: [
            {required: true, message: '请选择政治面貌', trigger: 'change'}
          ],
          merryStatus: [
            {required: true, message: '请选择婚姻状况', trigger: 'change'}
          ],
          isSoldier: [
            {required: true, message: '请选择是否服兵役', trigger: 'change'}
          ]
        }
      }
    },
    created () {
      if (sessionStorage.baseInfo) {
        var baseInfo = JSON.parse(sessionStorage.baseInfo)
        baseInfo.birth = new Date(baseInfo.birth)
        baseInfo.entryDate = new Date(baseInfo.entryDate)
        this.baseInfo = baseInfo
      }
    },
    methods: {
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.baseInfo)
            sessionStorage.setItem('baseInfo', JSON.stringify(this.baseInfo))
            this.$router.push({name: 'position'})
          }
        })
      }
    },
    components: { VHeader }
  }
</script>

<style lang="stylus">
.page-resume-baseinfo
  padding-bottom: 50px
</style>



