import Vue from 'vue'
import Router from 'vue-router'
import resumeIndex from '~/resume/index'
import baseInfo from '~/resume/base-info'
import contact from '~/resume/contact'
import education from '~/resume/education'
import family from '~/resume/family'
import position from '~/resume/position'
import work from '~/resume/work'
import technology from '~/resume/technology'
import shengao from '~/resume/shengao'

import userIndex from '~/user/index'
import interview from '~/user/interview'
import notSatisfied from '~/user/not-satisfied'
import resume from '~/user/resume'
import talnent from '~/user/talnent'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/resume',
      component: resumeIndex,
      children: [
        {
          path: '',
          component: baseInfo
        },
        {
          path: 'base-info',
          name: 'base-info',
          component: baseInfo
        },
        {
          path: 'contact',
          name: 'contact',
          component: contact
        },
        {
          path: 'education',
          name: 'education',
          component: education
        },
        {
          path: 'family',
          name: 'family',
          component: family
        },
        {
          path: 'position',
          name: 'position',
          component: position
        },
        {
          path: 'technology',
          name: 'technology',
          component: technology
        },
        {
          path: 'work',
          name: 'work',
          component: work
        },
        {
          path: 'shengao',
          name: 'shengao',
          component: shengao
        }
      ]
    },
    {
      path: '/user',
      component: userIndex,
      children: [
        {
          path: '',
          component: resume
        },
        {
          path: 'interview',
          name: 'interview',
          component: interview
        },
        {
          path: 'not-satisfied',
          name: 'not-satisfied',
          component: notSatisfied
        },
        {
          path: 'resume',
          name: 'resume',
          component: resume
        },
        {
          path: 'talnent',
          name: 'talnent',
          component: talnent
        }
      ]
    }
  ]
})



