<template>
  <div class="view-content">
    <v-header></v-header>
    <div class="content-slider">
      <el-menu theme="dark" >
        <router-link v-for="(item, index) in routerMenus"
          :to="{name: item.name}" :key="index">
          <el-menu-item :index="index + ''">
            <i :class="item.icon"></i><span>{{item.label}}</span>
          </el-menu-item>
        </router-link>
      </el-menu>
    </div>
    <div class="resume-content-router">
      <router-view></router-view>
    </div>
    <v-footer></v-footer>
  </div>
</template>

<script>
  import VHeader from './header'
  import VFooter from './footer'

  export default {
    data () {
      return {
        routerMenus: [
          { name: 'base-info', label: '基本信息', icon: 'el-icon-document' },
          { name: 'position', label: '职业信息专栏', icon: 'el-icon-menu' },
          { name: 'contact', label: '联系方式', icon: 'el-icon-menu' },
          { name: 'family', label: '家庭成员', icon: 'el-icon-menu' },
          { name: 'education', label: '教育经历', icon: 'el-icon-menu' },
          { name: 'work', label: '工作经历', icon: 'el-icon-menu' },
          { name: 'technology', label: '技能及个人特长', icon: 'el-icon-menu' },
          { name: 'shengao', label: '申告事项', icon: 'el-icon-menu' }
        ]
      }
    },
    components: { VHeader, VFooter }
  }
</script>

<style lang="stylus">
  .content-slider
    position: fixed
    top: 120px
    height: 100%
    width: 190px
  .resume-content-router
    padding-top: 120px
    padding-left: 238px
    padding-right: 30px

  .resume-block-title
    font-size: 16px
    font-weight: 500
    padding-bottom: 15px
    margin-bottom: 20px
    border-bottom: 1px solid #ddd
  .item-block
    display: flex
    .el-form-item
      width: 50%
    .el-input
      width: 346px
    &.btn-container
      justify-content: center
    &.col-3 .el-input
      width: 170px
    &.item-add
      justify-content: flex-end

  .form-item-group
    padding-bottom: 15px
    margin-bottom: 10px
    border-bottom: 1px solid #ddd

  .router-link-active .el-menu-item
    color: #09AD8A
    background-color: rgb(230, 247, 243)
    outline: 0
</style>



