<template>
  <div class="page-resume-shengao">
    <p class="resume-block-title">申告事项</p>
    <el-form :model="state" ref="state">
      <el-form-item label="您是否在两年内有发生过交通违章？若有，请详细列出"
        prop="breakRule"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"
        v-model="state.breakRule"></el-input>
      </el-form-item>

      <el-form-item label="您是否因违法行为而被拘捕、民事或刑事诉讼过？若有，请详细列出"
        prop="breakLaw"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"
        v-model="state.breakLaw"></el-input>
      </el-form-item>

      <el-form-item label="您是否被公司解除劳动合同或和公司协商解除劳动合同过？若有，请详细列出"
        prop="surrender"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"
        v-model="state.surrender"></el-input>
      </el-form-item>

      <el-form-item label="您是否有亲属朋友在雅玛多关联公司工作？若有，请列出姓名，部门名称及职位"
        prop="friends"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="请输入"
        v-model="state.friends"></el-input>
      </el-form-item>

      <el-form-item label="您是否同意公司对您进行工作地点的调动?"
        prop="workPlaceAgree"
        :inline-message="true"
        :rules="[
          {required: true, message: '请选择是否同意', trigger: 'change'}
        ]"
      >
        <el-radio-group v-model="state.workPlaceAgree">
          <el-radio label="同意"></el-radio>
          <el-radio label="不同意"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item
        prop="workPlaceInfo"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="可补充说明"
        v-model="state.workPlaceInfo"></el-input>
      </el-form-item>

      <el-form-item label="您是否同意接受公司为您安排的其它岗位？?"
        prop="planAgree"
        :inline-message="true"
        :rules="[
          {required: true, message: '请选择是否同意', trigger: 'change'}
        ]">
        <el-radio-group v-model="state.planAgree">
          <el-radio label="同意"></el-radio>
          <el-radio label="不同意"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        prop="planInfo"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="可补充说明"
        v-model="state.planInfo"></el-input>
      </el-form-item>

      <el-form-item label="您是否同意接受不超过一年的外派工作?"
        prop="businessTripAgree"
        :inline-message="true"
        :rules="[
          {required: true, message: '请选择是否同意', trigger: 'change'}
        ]"
      >
        <el-radio-group v-model="state.businessTripAgree">
          <el-radio label="同意"></el-radio>
          <el-radio label="不同意"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item
        prop="businessTripInfo"
        :rules="[
          {required: true, message: '请输入内容'}
        ]"
      >
        <el-input :autosize="textareaSize" type="textarea" placeholder="可补充说明"
        v-model="state.businessTripInfo"></el-input>
      </el-form-item>
    </el-form>
    <div class="item-block btn-container">
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="submit('state')">提交简历</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        state: {
          breakRule: '',
          breakLaw: '',
          surrender: '',
          friends: '',
          workPlaceAgree: '',
          workPlaceInfo: '',
          planAgree: '',
          planInfo: '',
          businessTripAgree: '',
          businessTripInfo: ''
        },
        textareaSize: { minRows: 5, maxRows: 8 }
      }
    },
    created () {
      if (sessionStorage.state) {
        this.state = JSON.parse(sessionStorage.state)
      }
    },
    methods: {
      formateTime (time) {
        time = new Date(time)
        var year = time.getFullYear()
        var month = time.getMonth() + 1
        var date = time.getDate()
        if (month < 10) {
          month = '0' + month
        }
        if (date < 10) {
          date = '0' + date
        }
        return year.toString() + month.toString() + date.toString()
      },
      prev () {
        this.$router.push({name: 'technology'})
      },
      submit (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (sessionStorage.baseInfo && sessionStorage.contactInfo && sessionStorage.educationList && sessionStorage.familyMembers && sessionStorage.positionInfo && sessionStorage.skill && sessionStorage.workList) {
              var baseInfo = JSON.parse(sessionStorage.baseInfo)
              var contcatInfo = JSON.parse(sessionStorage.contactInfo)
              var educationList = JSON.parse(sessionStorage.educationList)
              var familyMembers = JSON.parse(sessionStorage.familyMembers)
              var positionInfo = JSON.parse(sessionStorage.positionInfo)
              var skill = JSON.parse(sessionStorage.skill)
              var workList = JSON.parse(sessionStorage.workList)
              var newFamilyMembers = []
              var newWorkList = []
              var newEducationList = []
              baseInfo.birth = this.formateTime(baseInfo.birth)
              positionInfo.expirDate = this.formateTime(positionInfo.expirDate)
              positionInfo.workLicenseExpirDate = this.formateTime(positionInfo.workLicenseExpirDate)
              for (let i = 0; i < educationList.length; i++) {
                let obj = {}
                obj.beginDate = this.formateTime(educationList[i].startDate)
                obj.endDate = this.formateTime(educationList[i].endDate)
                obj.school = educationList[i].schoolName
                obj.profession = educationList[i].majorName
                obj.education = educationList[i].xl
                newEducationList.push(obj)
              }
              for (let i = 0; i < workList.length; i++) {
                let obj = {}
                obj.beginDate = this.formateTime(workList[i].startDate)
                obj.endDate = this.formateTime(workList[i].endDate)
                obj.company = workList[i].workPlace
                obj.post = workList[i].position
                obj.salary = workList[i].salary
                obj.reason = workList[i].leaveReason
                obj.reterence = workList[i].proveName
                obj.phone = workList[i].proveTel
                newWorkList.push(obj)
              }
              for (let i = 0; i < familyMembers.length; i++) {
                let obj = {}
                obj.name = familyMembers[i].name
                obj.relation = familyMembers[i].relation
                obj.age = familyMembers[i].age
                obj.residence = familyMembers[i].address
                obj.company = familyMembers[i].workPlace
                obj.post = familyMembers[i].position
                newFamilyMembers.push(obj)
              }
              var data = {
                name: baseInfo.name,
                sex: baseInfo.gender === 0 ? '男' : '女',
                num: baseInfo.idNumber,
                date_of_birth: baseInfo.birth,
                height: Number(baseInfo.height),
                weight: Number(baseInfo.height),
                nativeStr: baseInfo.address,
                politics: baseInfo.politicStatus,
                marriage: baseInfo.merryStatus,
                escuage: baseInfo.isSoldier,
                licensetype: positionInfo.driveType,
                licenselocation: positionInfo.certifacateAddress,
                licensevaliddate: positionInfo.expirDate,
                drivingex: positionInfo.driveTime,
                cartype: positionInfo.driveCarList.join(','),
                isworklicense: positionInfo.hasWorkLicense,
                worklicensevaliddate: positionInfo.workLicenseExpirDate,
                tbFamilyList: newFamilyMembers,
                tbEducationList: newEducationList,
                tbCompanyList: newWorkList,
                familyphone: contcatInfo.telNumber,
                phone: contcatInfo.phoneNumber,
                email: contcatInfo.email,
                birthplace: contcatInfo.houseAddress,
                birthcode: contcatInfo.houseAddressCode,
                residence: contcatInfo.nowAddress,
                residencecode: contcatInfo.nowAddressCode,
                iceperson: contcatInfo.emergencyPeople,
                relation: contcatInfo.emergencyPeopleRelation,
                icepersonphone: contcatInfo.emergencyPeoplePhone,
                iscomputer: skill.software,
                proskill: skill.training,
                otherskill: skill.languageLevel,
                interest: skill.interest,
                ispeccancy: this.state.breakRule,
                beillegal: this.state.breakLaw,
                retire: this.state.surrender,
                tranlocation: this.state.workPlaceAgree,
                reason1: this.state.workPlaceInfo,
                tranpost: this.state.planAgree,
                reason2: this.state.planInfo,
                tranlocation1year: this.state.businessTripAgree,
                reason3: this.state.businessTripInfo
              }
              this.$http.post('/candidate/addCandidate', JSON.stringify(data))
                .then(
                  this.$message('提交成功')
                )
            } else {
              this.$message('请完善您的信息后再提交')
            }
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-shengao
    padding-bottom: 50px
    .el-form-item:nth-last-of-type(2)
      margin-bottom: 0
    .el-form-item:nth-last-of-type(4)
      margin-bottom: 0
    .el-form-item:nth-last-of-type(6)
      margin-bottom: 0
</style>



