<template>
  <div class="page-resume-contact">
    <p class="resume-block-title">联系方式</p>
    <el-form label-width="100px" label-position="left" :model="contactInfo" ref="contactInfo"
     :rules="rules">
      <div class="item-block">
        <el-form-item label="家庭电话" prop="telNumber">
          <el-input placeholder="请输入家庭电话" v-model="contactInfo.telNumber"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" prop="phoneNumber">
          <el-input placeholder="请输入手机号码" v-model="contactInfo.phoneNumber"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="电子邮箱" prop="email">
          <el-input placeholder="请输入电子邮箱" v-model="contactInfo.email"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="户籍地址" prop="houseAddress">
          <el-input placeholder="请输入户籍地址" v-model="contactInfo.houseAddress"></el-input>
        </el-form-item>
        <el-form-item label="户籍邮编" prop="houseAddressCode">
          <el-input placeholder="请输入邮编" v-model="contactInfo.houseAddressCode"></el-input>
        </el-form-item>
      </div>

      <div class="item-block">
        <el-form-item label="目前住址" prop="nowAddress">
          <el-input placeholder="请输入目前住址" v-model="contactInfo.nowAddress"></el-input>
        </el-form-item>
        <el-form-item label="住址邮编" prop="nowAddressCode">
          <el-input placeholder="请输入住址邮编" v-model="contactInfo.nowAddressCode"></el-input>
        </el-form-item>
      </div>

      <div class="item-block col-3">
        <el-form-item label="紧急联系人" prop="emergencyPeople">
          <el-input placeholder="请输入紧急联系姓名" v-model="contactInfo.emergencyPeople">
          </el-input>
        </el-form-item>
        <el-form-item label="关系" prop="emergencyPeopleRelation">
          <el-input placeholder="请输入关系" v-model="contactInfo.emergencyPeopleRelation">
          </el-input>
        </el-form-item>
        <el-form-item label="手机号" prop="emergencyPeoplePhone">
          <el-input placeholder="请输入紧急联系手机号" v-model="contactInfo.emergencyPeoplePhone">
          </el-input>
        </el-form-item>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="next('contactInfo')">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      var checkPhoneNumber = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入手机号码'))
        } else if (!/^1[3|4|5|8][0-9]\d{8}$/i.test(value)) {
          callback(new Error('输入的手机号码格式不正确'))
        } else {
          callback()
        }
      }
      var checkTelNumber = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入电话号码'))
        } else if (!/^0\d{2,3}-?\d{7,8}$/i.test(value)) {
          callback(new Error('输入的电话号码格式不正确'))
        } else {
          callback()
        }
      }
      var checkEmail = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入邮箱'))
        } else if (!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/i.test(value)) {
          callback(new Error('输入的邮箱格式不正确'))
        } else {
          callback()
        }
      }
      return {
        contactInfo: {
          telNumber: '',
          phoneNumber: '',
          email: '',
          houseAddress: '',
          houseAddressCode: '',
          nowAddress: '',
          nowAddressCode: '',
          emergencyPeople: '',
          emergencyPeopleRelation: '',
          emergencyPeoplePhone: ''
        },
        rules: {
          telNumber: [
            {validator: checkTelNumber, trigger: 'blur'}
          ],
          phoneNumber: [
            {validator: checkPhoneNumber, trigger: 'blur'}
          ],
          email: [
            {validator: checkEmail, trigger: 'blur'}
          ],
          emergencyPeoplePhone: [
            {validator: checkPhoneNumber, trigger: 'blur'}
          ],
          houseAddress: [
            {required: true, message: '请输入户籍地址', trigger: 'blur'}
          ],
          houseAddressCode: [
            {required: true, message: '请输入户籍邮编', trigger: 'blur'}
          ],
          emergencyPeople: [
            {required: true, message: '请输入紧急联系姓名', trigger: 'blur'}
          ],
          nowAddress: [
            {required: true, message: '请输入现在住址', trigger: 'blur'}
          ],
          emergencyPeopleRelation: [
            {required: true, message: '请输入关系', trigger: 'blur'}
          ],
          nowAddressCode: [
            {required: true, message: '请输入住址邮编', trigger: 'blur'}
          ]
        }
      }
    },
    created () {
      if (sessionStorage.contactInfo) {
        this.contactInfo = JSON.parse(sessionStorage.contactInfo)
      }
    },
    methods: {
      prev () {
        this.$router.push({name: 'position'})
      },
      next (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            sessionStorage.setItem('contactInfo', JSON.stringify(this.contactInfo))
            this.$router.push({name: 'family'})
          }
        })
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-contact
    padding-bottom: 50px
</style>



