<template>
  <div class="content-view">
    <v-header></v-header>
    <div class="content-menu">
      <router-link v-for="(item, index) in routerMenus"
        :to="{name: item.name}" :key="index">
        <el-button ><p>
          <span>{{item.label}}</span>
        </p>
          <p>
          <span>{{item.label2}}</span>
        </p></el-button>
      </router-link>
    </div>
    <div class="user-content-route">
      <router-view/>
    </div>
  </div>
</template>

<script>
  import VHeader from './header'

  export default {
    data () {
      return {
        routerMenus: [
          { name: 'resume', label: '简历', label2: '管理' },
          { name: 'interview', label: '面试', label2: '管理' },
          { name: 'talnent', label: '人才', label2: '管理' },
          { name: 'not-satisfied', label: '不合', label2: '格库' }
        ]
      }
    },
    components: { VHeader }
  }
</script>

<style lang="stylus">
  .content-menu
    position: fixed
    top: 100px
    width: 50px
    height: 10px
    padding: 0 25px
    .el-button
      margin-bottom: 25px
  .user-content-route
    padding-left: 120px
    padding-top: 45px
    padding-right: 50px

  .router-link-active .el-button
    color: #e4ffed
    border-color: #0CA97D
    background-color: #0CA97D

</style>



