<template>
  <div class="header-user">
    <div class="header-user-content">
      <div class="left">
        <p><img src="../../assets/images/u1520.png" height="36" width="53" alt=""></p>
        <p>CtrHR</p>
        <p><i class="el-icon-menu"></i></p>
      </div>
      <div class="nav">
        <p><el-button>组织</el-button></p>
        <p><el-button>员工</el-button></p>
        <p><el-button>考勤</el-button></p>
        <p><el-button>招聘</el-button></p>
        <p><el-button>薪资</el-button></p>
        <p><el-button>行政</el-button></p>
      </div>
      <div class="right">
        <div class="manage-center">
          <el-dropdown>
            <span class="el-dropdown-link">
              <i class="el-icon-setting"></i>  管理中心<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>黄金糕</el-dropdown-item>
              <el-dropdown-item>狮子头</el-dropdown-item>
              <el-dropdown-item>螺蛳粉</el-dropdown-item>
              <el-dropdown-item disabled>双皮奶</el-dropdown-item>
              <el-dropdown-item divided>蚵仔煎</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="user-info">
          <el-dropdown>
            <span class="el-dropdown-link">
              <img class="user-avatar" src="../../assets/images/u1520.png">
              <span>程敏</span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>黄金糕</el-dropdown-item>
              <el-dropdown-item>狮子头</el-dropdown-item>
              <el-dropdown-item>螺蛳粉</el-dropdown-item>
              <el-dropdown-item disabled>双皮奶</el-dropdown-item>
              <el-dropdown-item divided>蚵仔煎</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {}
    }
  }
</script>

<style lang="stylus">
  .header-user
    position: fixed
    left: 0
    top: 0
    width: 100%
    background-color: rgb(9, 173, 138)
    z-index: 1000
    .header-user-content
      width: 1190px
      height: 50px
      line-height: 50px
      margin: 0 auto
      display: flex
      justify-content: center
      align-items: center
      .left, .nav, .right
        display: flex
        justify-content: center
        align-items: center
        text-align: center
      .left
        flex: 1
      .right
        flex: 2
      .nav
        flex: 5
        p
          flex: 1
        .el-button
          border: 0
          background-color: rgb(9, 173, 138)
          font-size: 16px
          color: rgba(255, 255, 255, 0.6)
      .left
        border-right: 1px solid #fff
        font-size: 20px
        color: #fff
        p
          flex: 1

      .user-info
        margin-left: 15px
        .user-avatar
          border-radius: 50%
          width: 30px
          height: 30px
    .el-dropdown
      font-size: 16px
      color: rgba(255, 255, 255, 0.6)
</style>



