import axios from 'axios'

const config = {
  baseURL: process.env.NODE_ENV === 'development' ? 'http://localhost:7001/hr_ganway' : '/hr_ganway',
  headers: {
    'Content-Type': 'application/json;charset=utf-8'
  }
}

let $ajax = axios.create(config)

export { $ajax }



