<template>
  <div class="page-resume-work">
    <p class="resume-block-title">工作经历（最近的5家）</p>
    <el-form label-width="85px" label-position="left">
      <div class="form-item-group"
        v-for="(item, index) in workList" :key="index">
        <div class="item-block">
          <el-form-item label="起始时间">
            <el-date-picker
              v-model="item.startDate"
              type="date"
              placeholder="请输入起始时间"
              @change="validate(item.startDate, '起始时间')">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="终止时间">
            <el-date-picker
              v-model="item.endDate"
              type="date"
              placeholder="请输入终止时间"
              @change="validate(item.endDate, '终止时间')"
              >
            </el-date-picker>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="工作单位">
            <el-input v-model="item.workPlace" placeholder="请输入工作单位" @blur="validate(item.workPlace, '工作单位')"></el-input>
          </el-form-item>
          <el-form-item  label="职位">
            <el-input v-model="item.position" placeholder="请输入职位" @blur="validate(item.position, '职位')"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="薪资">
            <el-input v-model="item.salary" placeholder="请输入薪资" @blur="validate(item.salary, '薪水')"></el-input>
          </el-form-item>
          <el-form-item label="离职原因">
            <el-input v-model="item.leaveReason" placeholder="请输入离职原因" @blur="validate(item.leaveReason, '离职原因')"></el-input>
          </el-form-item>
        </div>

        <div class="item-block">
          <el-form-item label="证明人">
            <el-input v-model="item.proveName" placeholder="请输入证明人" @blur="validate(item.proveName, '证明人')"></el-input>
          </el-form-item>
          <el-form-item label="证明人手机">
            <el-input v-model="item.proveTel" placeholder="请输入证明人手机" @blur="validate(item.proveTel, '证明人手机')"></el-input>
          </el-form-item>
        </div>
      </div>
      <div class="item-block item-add">
        <el-button @click="addList" icon="el-icon-plus">新增工作经历</el-button>
      </div>
    </el-form>
    <div class="item-block btn-container">
      <el-button-group>
        <el-button type="primary" icon="el-icon-arrow-left" @click="prev">上一步</el-button>
        <el-button type="primary" @click="next">下一步<i class="el-icon-arrow-right el-icon--right"></i></el-button>
      </el-button-group>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        workList: [{
          startDate: '',
          endDate: '',
          workPlace: '',
          position: '',
          salary: '',
          leaveReason: '',
          proveName: '',
          proveTel: ''
        }]
      }
    },
    created () {
      if (sessionStorage.workList) {
        var workList = JSON.parse(sessionStorage.workList)
        for (let i = 0; i < workList.length; i++) {
          workList[i].startDate = new Date(workList[i].startDate)
          workList[i].endDate = new Date(workList[i].endDate)
        }
        this.workList = workList
      }
    },
    methods: {
      validate (item, value) {
        if (!item) {
          this.$message('请输入' + value)
          return
        }
        if (value === '证明人手机') {
          if (!/^1[3|4|5|8][0-9]\d{8}$/i.test(value)) {
            this.$message('手机号码格式错误')
          }
        }
      },
      addList () {
        if (this.workList.length < 3) {
          this.workList.push({
            startDate: '',
            endDate: '',
            workPlace: '',
            position: '',
            salary: '',
            leaveReason: '',
            proveName: '',
            proveTel: ''
          })
        } else {
          this.$message('最多填写3段工作简历')
        }
      },
      prev () {
        this.$router.push({name: 'education'})
      },
      next () {
        for (let i = 0; i < this.workList.length; i++) {
          for (let item in this.workList[i]) {
            if (!this.workList[i][item]) {
              this.$message('请填写相关信息')
              return
            }
          }
          if (this.workList[i].startDate.getTime() >= this.workList[i].endDate.getTime()) {
            this.$message('起始年月不能大于终止年月')
            return
          }
          if (!/^1[3|4|5|8][0-9]\d{8}$/i.test(this.workList[i].proveTel)) {
            this.$message('手机号码格式错误')
            return
          }
        }
        sessionStorage.setItem('workList', JSON.stringify(this.workList))
        this.$router.push({name: 'technology'})
      }
    }
  }
</script>

<style lang="stylus">
  .page-resume-work
    padding-bottom: 50px
</style>



